<template>
    <v-container class="pa-0" style="position: relative; width: 100vw" align="center" fluid>

        <div v-if="isEventLoaded">
            <v-row class="ma-0 text-center justify-center" style="width: 100%; margin-bottom: 16px!important">

                <v-img
                        v-if="headerImg"
                        class="white--text align-end"
                        :src="headerImg"
                        style="overflow: visible; margin-bottom: 26px; height: 200px; min-width: 100%; z-index: 2!important;"
                >
                    <v-btn class="top-left-fixed-button" text icon color="#FFFFFF"
                           @click="$router.go(-1)">
                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                    <v-btn class="top-right-fixed-button" text dark
                           @click="editEvent">
                        {{editText}}
                    </v-btn>
                    <v-chip
                            class="centered-event-name"
                            color="primary"
                            block
                            x-large
                    >
                        <strong>{{ name }}</strong>&nbsp;
                    </v-chip>
                </v-img>

                <v-card class="text-center justify-center pa-2"
                        style="width: 95%;  padding-top: 36px!important; margin-top: -16px!important">
                    <p align="center">"{{description}}"</p>

                    <p align="center" class="primary--text"><b>{{date}}</b></p>

                    <div
                            class="scrolling-wrapper-flexbox text-center justify-center"
                            style=" width: calc(100% - 8px); margin-left: 4px; margin-top: 0px; margin-bottom: 16px"
                    >
                        <div
                                v-for="lan in involvedLanguages"
                                :key="lan" class="flag-card ma-1">
                            <v-avatar
                                    style="height: 16px !important; width: 16px !important; min-width: 16px!important;">
                                <img :src="createPathToFlagImage(lan)" :aspect-ratio="1"
                                     style=" object-position: center; object-fit: cover;">
                            </v-avatar>
                        </div>
                    </div>
                </v-card>
            </v-row>

            <v-row class="ma-0 text-center justify-center" style="width: 100%; margin-bottom: 16px!important">
                <v-card class="text-center justify-center pa-2" style="width: 95%;">
                    <v-card-title class="text-center justify-center pa-3" style="padding-bottom: 0px!important">
                        <v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-account-multiple
                        </v-icon>
                        &nbsp;{{participantsText}}
                        <v-btn
                                v-show="amIParticipating != null && !amIParticipating"
                                dark
                                fab
                                top
                                small
                                right
                                absolute
                                color="primary"
                                style="transform: translate(10%, 85%);"
                                @click="addParticipant"
                        >
                            <v-icon>mdi-account-plus</v-icon>
                        </v-btn>

                        <v-btn
                                v-show="amIParticipating != null && amIParticipating"
                                dark
                                fab
                                top
                                small
                                right
                                absolute
                                color="#e72222"
                                style="transform: translate(10%, 85%);"
                                @click="removeParticipant"
                        >
                            <v-icon>mdi-account-remove</v-icon>
                        </v-btn>
                    </v-card-title>


                    <v-row
                            style="max-height: 100px"
                            class="scroll-y"
                    >
                        <v-card-text v-if="participants == 0">
                            {{noParticipantsText}}
                        </v-card-text>

                        <div
                                v-if="participants != 0"
                                class="scrolling-wrapper-flexbox"
                        >
                            <div
                                    v-for="user in participants"
                                    :key="user.id" class="card ma-1">
                                <v-avatar v-if="user.profileImg"
                                          @click="openUserProfile(user.id)"
                                          style="height: 50px; !important; width: 50px; !important;">
                                    <img :src="user.profileImg" alt="Avatar" :aspect-ratio="1"
                                         style=" object-position: center; object-fit: cover;">
                                </v-avatar>
                            </div>
                        </div>
                    </v-row>
                </v-card>
            </v-row>

            <v-row class="ma-0" style="width: 100%;margin-bottom: 74px !important;" align="center"
                   justify="center">
                <v-card class="text-center justify-center pa-2" style="width: 95%">
                    <v-card-title class="text-center justify-center pa-3">
                        <v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-store</v-icon>
                        &nbsp;{{place}}
                    </v-card-title>
                    <v-card-text align="center" class="text-with-icon" style="padding-bottom: 0px">
                        {{address}}, {{city}}
                        <span v-if="distance != null">
                            <br/>
                            <v-icon style="display: inline;  vertical-align: top; font-size:18px;">
                                mdi-map-marker-outline
                            </v-icon>
                            {{distance.toFixed(2)}} km
                         </span>
                    </v-card-text>

                    <v-card-actions class="pa-0 justify-center">
                        <GmapMap
                                class="ma-2"
                                v-if="location"
                                :center="location"
                                :zoom="15"
                                style="width:98%;  height: 40vh; min-height: 240px"
                        >
                            <GmapMarker
                                    :position="location"
                                    :icon="eventPinIcon"
                            ></GmapMarker>
                        </GmapMap>
                    </v-card-actions>

                </v-card>
            </v-row>
            <v-btn
                    v-if="userRole == 'admin'"
                    style="z-index: 3; bottom: 80px"
                    fab
                    dark
                    fixed
                    right
                    color="#e72222"
                    @click="wantToRemoveEvent"
            >
                <v-icon>mdi-delete</v-icon>
            </v-btn>
            <div style="width: 100%; height: 90px" v-if="userRole == 'admin'"></div>
        </div>

        <v-dialog
                v-model="dialog"
                max-width="500px"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <h3 style="margin-bottom: 10px">{{aboutToRemoveEventText}}</h3>
                    <p style="margin-bottom: 7px">{{youSureYouWantToRemoveEventText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="dialog = false">{{cancelText}}</v-btn>
                    <v-btn color="primary" @click="deleteEvent">{{confirmText}}</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>

        <v-snackbar
                v-model="snackbar2"
                :timeout="4000"
                style="bottom: 76px"
                color="primary"
        >
            {{ snackbarText }}
            <v-btn

                    color="white"
                    text
                    @click="snackbar2 = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex';
    import {gmapApi} from 'vue2-google-maps'

    export default {
        name: "EventDescription",
        data() {
            return {
                headerImg: null,
                location: null,
                city: null,
                place: null,
                name: null,
                description: null,
                date: null,
                address: '',
                distance: null,
                isEventLoaded: false,
                participants: [],
                participantsText: null,
                noParticipantsText: null,
                confirmParticipationText: null,
                cancelParticipationText: null,
                amIParticipating: null,
                id: null,
                involvedLanguages: null,
                snackbar: false,
                snackbar2: false,
                snackbarText: null,
                closeText: null,
                dialog: false
            }
        },
        methods: {
            ...mapActions('events', ['getEventByID', 'getEventParticipants', 'addParticipantToEvent', 'removeParticipantFromEvent', 'removeEvent']),
            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },
            wantToRemoveEvent: function () {
                let self = this;
                self.dialog = true;
            },
            deleteEvent: function () {
                this.removeEvent(this.id).then(() => {
                    this.$router.replace({name: 'eventsHome'});
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            editEvent: function () {
                this.$router.push({name: 'eventEdit'});
            },
            setEventInfo(event) {
                this.headerImg = event.headerImg;
                this.location = {
                    'lat': event.location.coordinates.lat,
                    'lng': event.location.coordinates.lng
                };
                this.name = event.name;
                this.description = event.description;
                if (event.date instanceof Date) {
                    this.date = this.$i18n.d(event.date, 'short');
                } else {
                    this.date = this.$i18n.d(new Date(event.date.toDate()), 'short');
                }
                this.address = event.location.address;
                this.city = event.location.city;
                this.place = event.location.place;
                this.distance = event.distance;
                this.involvedLanguages = event.involvedLanguages;
            },
            openUserProfile(id) {
                if (id != this.$store.getters['user/getID']) {
                    this.$router.push({name: 'userProfileSearch', params: {id: id}});
                } else {
                    this.$router.push({name: 'profileHome'});
                }
            },
            getParticipants(eventID) {
                let self = this;
                return new Promise(function (resolve, reject) {
                    let resultGetEventParticipants = self.getEventParticipants(eventID);

                    resultGetEventParticipants.then((participants) => {
                        self.participants = participants;
                        resolve();
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                            reject();
                        });
                });
            },
            addParticipant: function () {
                let self = this;

                return new Promise(function (resolve) {
                    let resultAddParticipantToEvent = self.addParticipantToEvent(self.id);

                    resultAddParticipantToEvent.then(() => {
                        self.amIParticipating = self.$store.getters['events/amIParticipating']({
                            eventID: self.id,
                            userID: self.$store.getters['user/getID']
                        });

                        self.snackbarText = self.confirmParticipationText;
                        self.snackbar2 = true;

                        self.getParticipants(self.id).then(() => {
                            resolve();
                        })
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                });
            },
            removeParticipant: function () {
                let self = this;

                return new Promise(function (resolve) {
                    let resultRemoveParticipantFromEvent = self.removeParticipantFromEvent(self.id);

                    resultRemoveParticipantFromEvent.then(() => {
                        self.amIParticipating = self.$store.getters['events/amIParticipating']({
                            eventID: self.id,
                            userID: self.$store.getters['user/getID']
                        });

                        self.snackbarText = self.cancelParticipationText;
                        self.snackbar2 = true;

                        self.getParticipants(self.id).then(() => {
                            resolve();
                        })
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                });
            }
        },
        computed: {
            google: gmapApi,
            eventPinIcon: function () {
                return {
                    url: "https://imagehost.com.au//8bS3m", // url
                    scaledSize: this.google && new this.google.maps.Size(38, 41)
                };
            },
            youSureYouWantToRemoveEventText: function () {
                return this.$i18n.t('messages.TEXT_ARE_YOU_SURE_YOU_WANT_TO_REMOVE_EVENT');
            },
            aboutToRemoveEventText: function () {
                return this.$i18n.t('messages.TEXT_YOU_ARE_ABOUT_TO_REMOVE_EVENT');
            },
            userRole: function () {
                return this.$store.getters['user/getRole'];
            },
            editText: function () {
                return this.$i18n.t('messages.TEXT_EDIT');
            },
            cancelText: function () {
                return this.$i18n.t('messages.TEXT_CANCEL');
            },
            confirmText: function () {
                return this.$i18n.t('messages.TEXT_CONFIRM');
            }
        },
        created() {
            this.participantsText = this.$i18n.t('messages.TEXT_PARTICIPANTS');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.noParticipantsText = this.$i18n.t('messages.TEXT_NO_PARTICIPANTS_YET');
            this.confirmParticipationText = this.$i18n.t('messages.TEXT_CONFIRM_EVENT_PARTICIPATION');
            this.cancelParticipationText = this.$i18n.t('messages.TEXT_CANCEL_EVENT_PARTICIPATION');
        },
        mounted() {
            let self = this;
            let eventID = self.$route.params.id;
            self.id = eventID;
            let event = self.$store.getters['events/getEventByID'](eventID);

            if (event == undefined || event == null) {
                self.getEventByID(self.id).then((eventInfo) => {
                    event = eventInfo;
                    console.log("-->" + eventInfo)
                    self.amIParticipating = self.$store.getters['events/amIParticipating']({
                        eventID: self.id,
                        userID: self.$store.getters['user/getID']
                    });
                    self.setEventInfo(event);
                    self.getParticipants(eventID).then(() => {
                        self.isEventLoaded = true;
                    });
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            } else {
                self.amIParticipating = self.$store.getters['events/amIParticipating']({
                    eventID: self.id,
                    userID: self.$store.getters['user/getID']
                });
                self.setEventInfo(event);
                self.getParticipants(eventID).then(() => {
                    self.isEventLoaded = true;
                });
            }


            /*if (null == event) {
                self.getEventFirebase(eventID).then(function (event2) {
                    self.setEventInfo(event2);
                    self.isEventLoaded = true;
                })
                    .catch(function (error) {
                        console.log(error);
                    });
            } else {
                self.setEventInfo(event);
                self.isEventLoaded = true;
            }*/


        }

    }
</script>

<style scoped>
    .top-left-fixed-button {
        position: absolute !important;
        top: 0px !important;
        left: 0px !important;
        z-index: 2;
    }

    .scrolling-wrapper-flexbox {
        display: flex;
        flex-wrap: nowrap;
        overflow-x: auto;
        margin: 12px;
        margin-left: 36px;
        margin-right: 36px;
        padding-bottom: 6px;
    }

    .centered-event-name {
        position: absolute;
        top: 100%;
        left: 50%;
        padding: 10px;
        height: auto;
        transform: translate(-50%, -50%);
    }

    .top-left-fixed-button {
        position: fixed;
        top: 0px !important;
        left: 0px !important;
        z-index: 2;
    }

    .top-right-fixed-button {
        position: absolute;
        top: 0px !important;
        right: 0px !important;
        z-index: 2;
    }
</style>
